import {
	Typography,
	styled,
	FormGroup,
	FormControl,
	InputLabel,
	Input,
	Button,
	FormLabel,
	Radio,
	RadioGroup,
	FormControlLabel,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import { useState } from "react";
import { addUser } from "../Services/api";
const Text = styled(Typography)`
	margin-left: 40%;
`;
const Container = styled(FormGroup)`
	width: 50%;
	margin: 5% auto 0 auto;
	& div {
		margin-top: 20px;
	}
`;
const initialValues = {
	name: "",
	username: "",
	email: "",
	phone: "",
};

const AddUser = () => {
	const navigate = useNavigate();
	const [User, setUser] = useState({ gender: "" });
	const onValueChange = e => {
		setUser({ ...User, [e.target.name]: e.target.value });
	};
	const addUserDetails = async () => {
		await addUser(User);
		navigate("/all");
	};
	return (
		<Container>
			<Text variant="h4">Add User</Text>
			<FormControl>
				<InputLabel>userId</InputLabel>
				<Input
					onChange={e => onValueChange(e)}
					name="id"
				></Input>
			</FormControl>
			<FormControl>
				<InputLabel>First Name</InputLabel>
				<Input
					onChange={e => onValueChange(e)}
					name="f_name"
				/>
			</FormControl>
			<FormControl>
				<InputLabel>Last Name</InputLabel>
				<Input
					onChange={e => onValueChange(e)}
					name="l_name"
				/>
			</FormControl>
			<FormControl>
				<InputLabel>Email</InputLabel>
				<Input
					onChange={e => onValueChange(e)}
					name="email"
				></Input>
			</FormControl>
			<FormControl>
				<InputLabel>Phone</InputLabel>
				<Input
					onChange={e => onValueChange(e)}
					name="phone"
				></Input>
			</FormControl>
			<FormControl>
				<FormLabel id="demo-radio-buttons-group-label">Gender</FormLabel>
				<RadioGroup
					aria-labelledby="demo-radio-buttons-group-label"
					defaultValue=""
					name="gender"
					onChange={onValueChange}
				>
					<FormControlLabel
						value="female"
						control={<Radio />}
						label="Female"
					/>
					<FormControlLabel
						value="male"
						control={<Radio />}
						label="Male"
					/>
				</RadioGroup>
			</FormControl>
			<FormControl>
				<Button
					variant="contained"
					color="success"
					onClick={() => addUserDetails()}
				>
					{" "}
					Add User
				</Button>
			</FormControl>
		</Container>
	);
};

export default AddUser;
