import {
	Typography,
	styled,
	TableHead,
	TableRow,
	TableCell,
	TableBody,
	Button,
	Box,
} from "@mui/material";
import { Table } from "@mui/material";
import { Link } from "react-router-dom";
import { useEffect } from "react";
import { useState } from "react";
import { getUser, getUsers } from "../Services/api";
import { deleteUser } from "../Services/api";

const Text = styled(Typography)`
	margin-left: 40%;
`;
const Tbl = styled(Table)`
	margin: 30px auto 0 auto;
	border: 3px solid hotpink;
	border-radius: 30px;
	width: 90%;
	overflow-x: auto;
`;
const Head = styled(TableHead)`
	background: black;
	& th {
		color: white;
		font-family: poppins;
	}
`;

const AllUser = () => {
	const [Users, setUsers] = useState([]);
	useEffect(() => {
		getUserDetails();
	}, []);
	const getUserDetails = async () => {
		let alluser = await getUsers();
		setUsers(alluser.data);
	};
	// Creating a function to delete user details
	const DeleteUser = async id => {
		await deleteUser(id);
		getUserDetails();
	};
	return (
		<Box
			sx={{
				overflowX: "auto",
			}}
		>
			<Table
				sx={{
					minWidth: 200,
					overflow: "auto",
					maxWidth: "80%",
					margin: "80px auto 0",
				}}
			>
				<Head>
					<TableRow>
						<TableCell>ID</TableCell>
						<TableCell>First Name</TableCell>
						<TableCell>Last Name</TableCell>
						<TableCell>Email</TableCell>
						<TableCell>Gender</TableCell>
						<TableCell>Action</TableCell>
					</TableRow>
				</Head>
				<TableBody>
					{Users.map((user, index) => (
						<TableRow key={index}>
							<TableCell>{user.id}</TableCell>
							<TableCell>{user.f_name}</TableCell>
							<TableCell>{user.l_name}</TableCell>
							<TableCell>{user.email}</TableCell>
							<TableCell>{user.gender}</TableCell>
							<TableCell>
								<Button
									variant="contained"
									color="success"
									style={{ marginRight: "5px" }}
									component={Link}
									to={`/edit/${user._id}`}
									sx={{
										display: "inline-block",
										margin: "5px auto",
									}}
								>
									Edit
								</Button>
								<Button
									variant="contained"
									color="error"
									onClick={() => DeleteUser(user._id)}
									sx={{
										display: "inline-block",
										margin: "5px auto",
									}}
								>
									Delete
								</Button>
							</TableCell>
						</TableRow>
					))}
				</TableBody>
			</Table>
		</Box>
	);
};
export default AllUser;
