import axios from "axios";

const API_URL = "api";

export const addUser = async data => {
	try {
		return await axios.post(`/${API_URL}/users/addUser`, data);
	} catch (error) {
		console.log("The error is: ", error.message);
	}
};

// Creating an API to showUsers

export const getUsers = async data => {
	try {
		return await axios.get(`/${API_URL}/users/`);
	} catch (error) {
		console.log("The error is : ", error.message);
	}
};

export const getUser = async data => {

	return await axios.get(`/${API_URL}/users/${data}`);

};

//  Creating an API to edit an user
export const editUser = async (data) => {
	try {
		return await axios.put(`/${API_URL}/users/edit`, data);
	} catch (error) {
		alert("error occured while editing");
		console.log("The error in edit API is : ", error.message);
	}
};

// Creating an API to delete an user
export const deleteUser = async _id => {
	try {
		axios
			.post(`/${API_URL}/delete/user`, { _id })
			.then(res => console.log(res));
	} catch (error) {
		console.log("The error in delete API is : ", error.message);
	}
};
